/****** Reset CSS *******/
/*** box sizing border-box ***/
*,
*::before,
*::after {
  box-sizing: border-box;
}

/*** link ***/
a,
a:visited {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

/*** button ***/
button {
  background-color: transparent;
  color: inherit;
  border-width: 0;
  padding: 0;
  cursor: pointer;
}

/*** margin 0 ***/
body,
h1,
h2,
h3,
h4,
h5,
h6,
figure,
ul,
p,
table,
fieldset {
  margin: 0;
}

/*** list ***/
ul,
ol,
dd {
  list-style: none;
  padding: 0;
  margin: 0;
}

/*** h(n) ***/
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: 700;
}

/*** cite ***/
cite {
  font-style: normal;
}

/*** fieldset ***/
fieldset {
  border-width: 0;
  padding: 0;
}

:root {
  --fontSize: 16px;
  --lh: 32px;
  --base: 26;
  --base-r: calc(var(--base) + 180);
  --textColor: hsl(var(--base), 6%, 23%);
  --textColor-g: hsl(var(--base), 6%, 49%);
  --textColor-w: hsl(var(--base), 6%, 97%);
  --textColor-r: hsl(var(--base-r), 6%, 23%);
  --textColor-rg: hsl(var(--base-r), 6%, 49%);
  --textColor-rw: hsl(var(--base-r), 6%, 97%);
  --backGround: hsl(var(--base), 20%, 89%);
  --backGround-l: hsl(var(--base), 20%, 95%);
  --backGround-d: hsl(var(--base), 20%, 35%);
  --backGround-r: hsl(var(--base-r), 80%, 31%);
  --backGround-rl: hsl(var(--base-r), 80%, 99%);
  --backGround-rd: hsl(var(--base-r), 80%, 11%);
  --linkColor: hsl(var(--base), 76%, 41%);
  --linkColor-r: hsl(var(--base-r), 76%, 41%);
}
@media ((min-width: 375px)) {
  :root {
    --fontSize: 17px;
  }
}
@media ((min-width: 768px)) {
  :root {
    --fontSize: 18px;
  }
}
@media ((min-width: 1200px)) {
  :root {
    --fontSize: 19px;
  }
}
@media ((min-width: 1400px)) {
  :root {
    --fontSize: 20px;
  }
}

html {
  font-size: var(--fontSize);
  scroll-behavior: smooth;
}

body {
  font-family: "BIZ UDPMincho", serif;
  width: 100%;
}

.icon {
  width: 1em;
  height: 1em;
  fill: currentColor;
  margin-right: 0.25em;
}

.hidden {
  position: absolute;
  top: -9999px;
  left: -9999px;
}

#header {
  display: flex;
  align-items: center;
  height: 3rem;
  background: var(--backGround-rl);
}
#header .logo {
  width: 100px;
  height: 27.1428571429px;
  fill: #f2849b;
  margin-left: 8px;
}
@media ((min-width: 992px)) {
  #header .logo {
    width: 116.6666666667px;
    height: 31.6666666667px;
    margin-left: 1rem;
  }
}

#navigation .line {
  background: currentColor;
}

#navigation {
  background: var(--backGround-rl);
  color: var(--textColor-r);
}
#navigation .line {
  background: currentColor;
}
#navigation .container.top {
  transform: translateY(-3px) scaleX(0.88235);
}
#navigation .container.bottom {
  transform: translateY(3px) scaleX(0.88235);
}
#navigation input:checked + label .container .line.bottom {
  transform: rotateZ(45deg);
}
#navigation input:checked + label .container .line.top {
  transform: rotateZ(-45deg);
}
#navigation input:checked + label .container.bottom {
  transform: none;
}
#navigation input:checked + label .container.top {
  transform: none;
}
#navigation input:checked + label .line.bottom {
  transform: none;
  transition-delay: 0.1s;
}
#navigation input:checked + label .line.top {
  transform: none;
  transition-delay: 0.1s;
}
#navigation .container {
  transition: transform 0.2s ease-in-out 0.1s;
}
#navigation input:checked + label .container {
  transition-delay: 0s;
}
#navigation .line {
  transition: transform 0.2s ease-in-out;
}
#navigation button {
  all: unset;
  cursor: pointer;
  display: block;
}
#navigation button div {
  pointer-events: none;
}
#navigation .burger {
  height: 31px;
  width: 31px;
  display: block;
  position: absolute;
  top: calc((3rem - 31px) / 2);
  right: calc((3rem - 31px) / 2);
}
#navigation .container {
  position: absolute;
  left: 7px;
  top: 15px;
}
#navigation .line {
  height: 1.5px;
  border-radius: 3px;
  width: 17px;
}
#navigation input {
  display: none;
}
#navigation .global-navigation {
  background: var(--backGround-rl);
  color: currentColor;
  height: 0%;
  overflow: hidden;
  position: absolute;
  top: 3rem;
  left: 0;
  right: 0;
  text-align: center;
  align-items: center;
  z-index: 1000;
  display: grid;
  grid-template-rows: repeat(4, 3rem);
  grid-template-columns: 1fr;
  justify-content: center;
  padding: 0 18%;
}
#navigation input:checked ~ .global-navigation {
  height: 100%;
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.3);
  padding-top: 2rem;
  transition: 0.2s ease-in-out 0.1s;
}
#navigation li {
  padding: 5px 0;
  border-bottom: 1px solid currentColor;
  margin-top: 1rem;
  font-size: 0.9em;
}
#navigation a:hover,
#navigation a:active {
  opacity: 0.8;
}
@media ((min-width: 768px)) {
  #navigation input,
  #navigation button,
  #navigation .burger {
    display: none;
  }
  #navigation .global-navigation,
  #navigation input:checked ~ .global-navigation {
    box-shadow: none;
    height: 3rem;
    background: transparent;
    color: var(--textColor);
    transition: none;
    justify-content: end;
    top: 0;
    left: auto;
    right: 1rem;
    grid-template-columns: repeat(4, auto);
    grid-template-rows: 3rem;
    padding: 0;
  }
  #navigation li {
    margin: 0 1rem;
    border: none;
    font-size: 0.75em;
    line-height: 1.5rem;
    font-weight: bold;
  }
  #navigation a:hover {
    text-decoration: underline;
  }
}

#footer {
  background: var(--backGround-rd);
  color: var(--textColor-w);
  margin-top: calc(var(--lh) * 4);
  padding: 40px 10px 0;
  font-size: 0.6em;
}
@media ((min-width: 375px)) {
  #footer {
    font-size: 0.75em;
  }
}
@media ((min-width: 768px)) {
  #footer {
    text-align: center;
  }
}
#footer .icon:hover, #footer .icon:active {
  fill: var(--textColor-rg);
}
#footer .sns .icon {
  height: 40px;
  width: 40px;
  margin: 10px 20px 0 0;
}
@media ((min-width: 768px)) {
  #footer .sns .icon {
    height: 50px;
    width: 50px;
    margin: 30px 40px 0 0;
  }
}
#footer .sns a:last-child .icon {
  margin-right: 0;
}
#footer .navigation {
  padding-top: 30px;
}
#footer .navigation a:hover,
#footer .navigation a:active {
  color: var(--textColor-rg);
}
#footer .navigation li {
  padding: 5px 0;
}
@media ((min-width: 768px)) {
  #footer .navigation {
    padding-top: 50px;
  }
  #footer .navigation > ul {
    display: flex;
    justify-content: center;
    gap: 0 20px;
  }
}

.copyright-powered {
  padding: 20px 0 10px;
  font-family: sans-serif;
}
@media ((min-width: 768px)) {
  .copyright-powered {
    text-align: center;
    font-size: 0.9em;
  }
}

#page404 {
  text-align: center;
}
#page404 .title {
  font-size: 1rem;
  padding-top: calc(var(--lh) * 2);
  font-family: sans-serif;
}
#page404 p {
  font-size: 0.9em;
  line-height: 1.5;
  padding-top: var(--lh);
}
#page404 a {
  color: var(--linkColor-r);
  text-decoration: underline;
}
#page404 a:visited {
  color: var(--linkColor-r);
}
#page404 a:hover, #page404 a:active {
  color: inherit;
}
#page404 .navigation {
  display: flex;
  font-size: 0.8em;
  justify-content: space-around;
  line-height: var(--lh);
  list-style: none;
  padding-top: 1em;
  max-width: 600px;
  margin: auto;
}